"use client";

import { useState, useEffect } from "react";
import { motion } from "framer-motion";

const links = [
  { href: "#about", label: "About" },
  { href: "#skills", label: "Skills" },
  { href: "#projects", label: "Projects" },
  { href: "#education", label: "Education" },
  { href: "#contact", label: "Contact" }
];

export default function Navbar() {
  const [scrolled, setScrolled] = useState(false);

  useEffect(() => {
    const onScroll = () => setScrolled(window.scrollY > 10);
    window.addEventListener("scroll", onScroll);
    return () => window.removeEventListener("scroll", onScroll);
  }, []);

  return (
    <motion.header
      initial={{ y: -40, opacity: 0 }}
      animate={{ y: 0, opacity: 1 }}
      transition={{ duration: 0.5 }}
      className={`fixed top-0 inset-x-0 z-40 ${
        scrolled ? "backdrop-blur-md bg-background/80" : "bg-transparent"
      } border-b border-white/5`}
    >
      <nav className="section-container flex items-center justify-between py-4">
        <div className="flex items-center gap-2">
          <div className="h-8 w-8 rounded-full bg-gradient-to-br from-accent to-accentOrange shadow-glow" />
          <div className="flex flex-col">
            <span className="font-display text-xs tracking-[0.25em] uppercase text-muted">
              Portfolio
            </span>
            <span className="font-display text-sm sm:text-base neon-text">
              Mir Suhaib
            </span>
          </div>
        </div>
        <div className="hidden md:flex items-center gap-8 text-xs font-medium uppercase tracking-[0.2em] text-muted">
          {links.map((link) => (
            <a
              key={link.href}
              href={link.href}
              className="hover:text-accent transition-colors"
            >
              {link.label}
            </a>
          ))}
        </div>
      </nav>
    </motion.header>
  );
}

